.radio {
    appearance: none;
    height: 20px;
    width: 20px;

    cursor: pointer;
    border-radius: 50%;
    outline: none;
    background: #e1e1e1;

}

.radio:hover{

    border: 3px solid dodgerblue;
}

.radio:checked{
    background: red;
    border: 3px solid dodgerblue;
}

.radio:focus{
    border: 3px solid dodgerblue;
}

.radio::before {
}


.radio:checked::before {
}

.label {
    cursor: pointer;
}

.options {
